import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'


import List from '@/components/list/listPage'
import setUp from '@/components/setUp/setUpPage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      children: [
        {
          path: 'list',
          name: 'List',
          component: List
        },{
          path: 'setUp',
          name: 'setUp',
          component: setUp
        }
      ]
    }
  ]
})



